        background-size: 500px;\n    }\n\n    #cta {\n        background-image: url('./images/bg-simplify-section-mobile.svg');\n\n    }\n}\n\n.hamburger {\n    cursor: pointer;\n    width: 24px;\n    height: 24px;\n    transition: all 0.25s;\n    position: relative;\n}\n  \n.hamburger-top,\n.hamburger-middle,\n.hamburger-bottom {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 24px;\n    height: 2px;\n    background: #000;\n    transform: rotate(0);\n    transition: all 0.5s;\n}\n  \n.hamburger-middle {\n    transform: translateY(7px);\n}\n  \n.hamburger-bottom {\n    transform: translateY(14px);\n}\n  \n.open {\n    transform: rotate(90deg);\n    transform: translateY(0px);\n}\n  \n.open .hamburger-top {\n    transform: rotate(45deg) translateY(6px) translate(6px);\n}\n  \n.open .hamburger-middle {\n    display: none;\n}\n  \n.open .hamburger-bottom {\n    transform: rotate(-45deg) translateY(6px) translate(-6px);\n}\n\n/* carousel */\n\n.carousel {\n    position: relative;\n    height: 400px;\n}\n\n.div-absolute {\n    position: relative;\n    width: 100%;\n    padding: 5rem 0rem;\n}\n\n.slide {\n    position: absolute;\n    opacity: 0;\n    transition: opacity 200ms ease-in-out;\n    transition-delay: 200ms;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%,-50%);\n\n}\n\n.slide[data-active] {\n    opacity: 1;\n    transition-delay: 0ms;\n}\n\n.carousel-button {\n    position: absolute;\n    z-index: 10;\n    background: none;\n    border: none;\n    font-size: 4rem;\n    top:50%;\n    transform: translateY(-50%);\n    cursor: pointer;\n    border-radius: .25rem;\n    padding:0 0.5rem;\n    background-color: rgba(0,0,0,0.1);\n}\n\n.carousel-button:hover,\n.carousel-button:focus {\n    background-color: rgba(0,0,0,0.2);\n}\n\n.carousel-button:focus {\n    outline: 1px solid black;\n}\n\n.carousel-button.prev {\n    left: 1rem;\n}\n\n.carousel-button.next {\n    right: 1rem;\n}",null],"sourceRoot":""}]);
